.companies {
  padding: 101px 0 128px 0;
  border-bottom: 1px solid rgba(197, 203, 226, 0.5);

  &-about {
    padding: 101px 0 147px 0;
    border-bottom: none;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 80px;
    font-weight: 700;
    font-size: 36px;
    line-height: 57px;
    color: var(--title-color);

    & span {
      color: var(--accent-color);
    }
  }

  &__content {
    min-width: 1052px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
