.nav {
  display: flex;
  align-items: center;

  &__list {
    margin-right: 30px;
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 60px;
    }
  }

  &__link {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: var(--text-light-color);
  }

  &__btn {
    background-color: var(--accent-color);
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: var(--light-color);
  }
}
