.code {
  padding: 65px 0 95px 0;
  background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 62px;
    color: var(--title-color);
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 97px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__content {
    margin-bottom: 97px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 36px;
  }

  &__item {
    padding: 40px;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
  }

  &__item-title {
    margin: 0;
    margin-bottom: 32px;
    font-size: 28px;
    line-height: 31px;
    color: var(--title-dark-color);
  }

  &__item-text {
    margin: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__descr {
    display: flex;
    align-items: center;
    gap: 38px;
  }

  &__descr-content {
    max-width: 416px;
  }

  &__descr-title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 57px;
    color: var(--title-color);
  }

  &__descr-text {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }
}
