.innovation {
  padding: 120px 0 30px 0;

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 57px;
    color: var(--title-color);
    text-align: center;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 68px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
    text-align: center;
  }

  &__content {
    min-width: 100%;
    padding: 45px 0;
  }

  &__item {
    min-height: 119px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    user-select: none;
  }

  &__btns {
    display: flex;
    align-items: center;
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;

    &:first-child {
      margin-right: 8px;
    }

    & svg {
      fill: #C2D1D9;
      transition: all .3s ease-in-out;
    }

    &:hover svg {
      fill: var(--dark-color);
    }
  }
}
