.footer {
  background-color: var(--dark-color);

  &__top {
    padding: 85px 0 65px 0;
    display: flex;
    justify-content: space-between;
  }

  &__descr {
    display: flex;
    flex-direction: column;
  }

  &__logo {
    margin-bottom: 25px;
  }

  &__text {
    margin: 0;
    max-width: 287px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--light-color);
  }

  &__nav {
    display: flex;
  }

  &__list {
    &:not(:last-child) {
      margin-right: 97px;
    }
  }

  &__item {
    margin-bottom: 30px;
  }

  &__link {
    margin: 0;
    font-size: 18px;
    line-height: 21px;
    color: var(--light-color);
  }

  &__social {
    height: 16px;
    margin-top: 40px;

    & img {
      height: 100%;

      &:not(:last-child) {
        margin-right: 15px;
      }
    }
  }

  &__bottom {
    border-top: 1px solid rgba(197, 203, 226, 0.5);
    padding: 29px 0;
  }

  &__container-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: var(--light-color);
  }

  &__rule {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-decoration-line: underline;
    color: var(--light-color);
  }
}
