.products {
  padding: 106px 0 108px 0;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 57px;
    color: var(--title-color);
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 97px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: var(--text-dark-color);
  }

  &__content {
    min-width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 32px;
  }

  &__item {
    position: relative;
    flex: 0 1 50%;
    min-height: 530px;
    padding: 64px 62px;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.08);
    border-radius: 8px;
    overflow: hidden;
  }

  &__item-img {
    margin-bottom: 46px;
  }

  &__item-text {
    margin: 0;
    margin-bottom: 32px;
    max-width: 352px;
    font-weight: 400;
    font-size: 24px;
    line-height: 31px;
    color: var(--text-dark-color);
  }

  &__item-btn {
    position: relative;
    transition: all .3s ease-in-out;
    z-index: 100;

    & img {
      margin-right: 8px;
    }
  }

  &__item-bg {
    position: absolute;
    bottom: 0;
    left: 10px;
    z-index: 1;
  }
}
