.stats {
  padding-bottom: 83px;

  &__container {
    display: flex;
    justify-content: center;
  }

  &__content {
    display: flex;
    gap: 32px;
  }

  &__item {
    padding-top: 53px;
    position: relative;
    min-width: 316px;
    min-height: 318px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: var(--light-color);
    box-shadow: 0px 0px 8px rgba(22, 23, 72, 0.07), 0px 25px 35px rgba(22, 23, 72, 0.05);
    border-radius: 8px;
    overflow: hidden;
  }

  &__title {
    margin: 0;
    margin-bottom: 18px;
    font-weight: 700;
    font-size: 56px;
    line-height: 62px;
    color: var(--title-color);
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    text-align: center;
    color: var(--text-dark-color);

  }

  &__img {
    position: absolute;
    bottom: 0;
  }
}
