.hero {
  position: relative;
  padding: 229px 0 210px 0;
  background: linear-gradient(252.91deg, #E3E7F6 3.68%, rgba(227, 231, 246, 0) 97.82%);

  &-products {
    padding: 290px 0 146px 0;
    background: linear-gradient(247.11deg, #E3E7F6 3.56%, rgba(227, 231, 246, 0) 115.17%);
  }

  &-about {
    padding: 289px 0 212px 0;
    background: linear-gradient(247.11deg, #E3E7F6 3.56%, rgba(227, 231, 246, 0) 115.17%);
  }

  &-career {
    padding: 278px 0 227px 0;
    background: linear-gradient(247.11deg, #E3E7F6 3.56%, rgba(227, 231, 246, 0) 115.17%);
  }

  &-contact {
    padding: 195px 0 139px 0;
    background: linear-gradient(247.11deg, #E3E7F6 3.56%, rgba(227, 231, 246, 0) 115.17%);
  }

  &__container {
    display: flex;
  }

  &__content {
    max-width: 570px;

    &-products {
      max-width: 467px;
    }

    &-about {
      max-width: 447px;
    }

    &-career {
      max-width: 598px;
    }

    &-contact {
      max-width: 502px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 32px;
    font-weight: 700;
    font-size: 48px;
    line-height: 62px;
    color: var(--title-color);

    & > span {
      font-size: 48px;
      line-height: 62px;
      color: var(--accent-color);
    }

    &-career, &-contact {
      margin-bottom: 40px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 40px;
    max-width: 512px;
    font-weight: 400;
    font-size: 22px;
    line-height: 33px;
    color: var(--text-dark-color);

    &-products {
      max-width: 444px;
    }

    &-about {
      font-size: 28px;
      line-height: 36px;
      max-width: 390px;
    }

    &-contact {
      max-width: 502px;
      font-size: 18px;
    }
  }

  &__buttons {
    max-width: 316px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__btn {
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: var(--accent-color);
      color: var(--light-color);
    }
  }

  &__image {
    position: absolute;
    bottom: 0;
    right: 75px;

    &-products {
      right: 200px;
    }

    &-about {
      right: 200px;
      bottom: 20px;
    }

    &-career {
      right: 125px;
      bottom: -4px;
    }

    &-contact {
      right: 250px;
      bottom: -4px;
    }
  }
}
