.info {
  padding: 73px 0;
  background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);

  &-products {
    padding: 101px 0 111px 0;
    background: none;
  }

  &-about {
    padding: 104px 0 121px 0;
    background: none;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &-about {
      justify-content: flex-start;
    }
  }

  &__content {
    max-width: 416px;
    display: flex;
    flex-direction: column;

    &-products {
      max-width: 419px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 700;
    font-size: 44px;
    line-height: 57px;
    color: var(--title-color);

    &-products {
      margin-bottom: 32px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 16px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);
  }

  &__text {
    margin: 0;
    max-width: 399px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: var(--text-dark-color);

    &:not(:last-child) {
      margin-bottom: 25px;
    }
  }

  &__btn {
    margin: 0;
    width: fit-content;
    font-size: 16px;
    line-height: 150%;
    letter-spacing: 0.005em;
    color: var(--accent-color);
  }

  &__img {
    &-about {
      margin-right: 60px;
    }
  }
}

.info2 {
  padding: 110px 0;
  background: linear-gradient(335.73deg, #E3E7F6 12.46%, rgba(227, 231, 246, 0) 84.19%);

  &-products {
    padding: 0 0 52px 0;
    background: none;
  }

  &-about {
    padding: 150px 0;
    background: linear-gradient(268.52deg, #E3E7F6 -52.27%, rgba(227, 231, 246, 0) 138.23%);
  }

  &__container {
    &-about {
      align-items: flex-start;
    }
  }

  &__title {
    &-products {
      margin-bottom: 32px;
    }
  }

  &__content {
    max-width: 363px;

    &-products {
      max-width: 470px;
    }

    &-about {
      max-width: 416px;
    }
  }

  &__subtitle {
    &-about {
      max-width: 334px;
      margin-bottom: 40px;
    }
  }

  &__text {
    &-products {
      max-width: 100%;
    }
  }

  &__btn {
    &-about {
      position: relative;
    }
  }

  &__accent {
    user-select: none;
    cursor: initial;
    position: absolute;
    top: 125%;
    left: 45%;
  }
}

