.about {
  padding: 136px 0;
  background: linear-gradient(342.12deg, #E3E7F6 -6.53%, rgba(227, 231, 246, 0) 87.81%);

  &-career {
    padding: 112px 0;
  }

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    max-width: 700px;
    font-weight: 700;
    font-size: 44px;
    line-height: 62px;
    color: var(--title-color);
    text-align: center;

    &-career {
      max-width: 710px;
      margin-bottom: 16px;
    }
  }

  &_subtitle {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    text-align: center;
    color: var(--text-dark-color);
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }
}
